<template>
  <div
    class="message-area"
    ref="container"
    v-on:scroll="handleScroll"
  >
    <vue-loaders v-if="loading" name="ball-pulse" color="orange" scale="1"></vue-loaders>
    <div
      v-if="messages.length > 0"
    >
      <div
        class="wrapper"
        v-for="(message, id) in messages"
        :key = id
      >
        <div
          :class="{ 'message-row': true }"
        >
          <div
            class="user-thumbnail"
          >
            <img
              v-if="message.speaker.thumbnail"
              :src="hostName + message.speaker.thumbnail"
            >
            <img
              v-else
              src="@/assets/images/no-image.png"
            >
          </div>
          <div class="message-body">
            <div class="speaker-name">{{ message.speaker.name }}</div>
            <p class="message-text" v-html="escapeHtml(message.message)" v-linkified ></p>
            <div
              v-if="message.attachments.length > 0"
              :class="{ 'attachment-row': true }"
            >
              <MainSectionMessageAreaFileThumbnail
                v-for="(attachment, id) in message.attachments"
                :key = id
                class="file-thumbnail"
                :fileInfo = attachment
              />
            </div>
          </div>
          <span class="sent-at">
          {{ message.sent_at | formatDateTime }}
          </span>
        </div>
      </div>
    </div>
    <div v-else>
      <p class="no-message-found">メッセージがありません</p>
    </div>
  </div>
</template>
<style scoped>
  div.main-panel .message-area {
    width: 100%;
    height: 85%;
    background-color: white;
    z-index: 2;
    overflow-y:  scroll;
    padding:10px 1rem;
  }
  .wrapper {
    width: 100%;
    border-bottom: solid lightgray 1px;
  }
  .attachment-row {
    display:flex;
    margin: 5px;
  }

  /* message row */
  .message-row {
    display: flex;
    position: relative;
    padding: 2px;
    margin: 5px 0 0 0;
  }
  .message-row.notme {
    flex-direction : row
  }

  .user-thumbnail {
    width: 10%;
    text-align: center;
    margin-right: 3px;
  }
  .user-thumbnail > img {
    width : 30px;
    height: 30px;
    border-radius: 50%;
  }
  .message-body {
    width: 90%;
    min-height:  50px;
    text-align: left;
  }
  .speaker-name {
    color: blue;
    display: block;
    width: 100%
  }
  .message-text {
    font-size: 14px;
    padding: 5px;
  }
  .sent-at {
    color: gray;
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 12px;
    display: inline-block
  }

  .tags {
    display: flex;
    font-size: 12px;
    margin: 0 1rem
  }
  .kidoku {
    display: inline-block;
    margin-right: 1rem;
    font-weight: bold
  }
  .read {
    color: gray
  }
  .notread {
    color: red
  }
  .no-message-found {
    color: gray;
  }
</style>
<script>
import MainSectionMessageAreaFileThumbnail from './MainSectionMessageAreaFileThumbnail'
import moment from 'moment'
import basemixin from '../mixins/basemixin'
export default {
  mixins: [basemixin],
  data () {
    return {
      isFirstDataFetch: true
    }
  },
  props: {
  },
  components: {
    MainSectionMessageAreaFileThumbnail
  },
  watch: {
    'activeRoomId': function (val) {
    }
  },
  methods: {
    toBottom: function () {
      var container = this.$refs.container
      container.scrollTop = container.scrollHeight
    },
    handleScroll: async function (evt) {
      const target = evt.target
      const position = target.scrollTop
      if (position == 0) {
        target.removeEventListener('scroll', this.handleScroll)
        // get current first child
        
        const firstChildElement = target.firstElementChild
        const rect = firstChildElement.getBoundingClientRect();
        await this.$store.dispatch('chat/getNextRoomMessages')
        this.isFirstDataFetch = false
        var offset = firstChildElement.offsetTop - rect.y
        target.scrollTop = offset
        target.addEventListener('scroll', this.handleScroll)
      }
    },
    escapeHtml: function (string) {
      string = string.replace(/(?:\r\n|\r|\n)/g, '<br/>')
      return string
    }
  },
  filters: {
    toKidoku: function (bool) {
      if (bool) {
        return '既読'
      } else {
        return '未読'
      }
    },
    formatDateTime: function (val) {
      return moment(val * 1000).format('M/D H:MM')
    }
  },
  mounted () {
    this.$refs.container.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy () {
    this.$refs.container.removeEventListener('scroll', this.handleScroll);
  },
  updated () {
    if (this.isFirstDataFetch) {
      this.toBottom()
    }
  }
}
</script>



